// import * as React from 'react';
import React, {useEffect, useState} from 'react';
import { useHistory } from "react-router-dom";


import CssBaseline from '@mui/material/CssBaseline';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Axios from 'axios';
import "./login.css";


import img1 from '../images/sign.png'






const AdminSignup = () => {
  const [value, SetValue] = useState("");  //mbti
  const [Id, SetId] = useState("");  //Id
  const [Pw, SetPw] = useState("");  //pw
  const [Email, SetEmail] = useState("");  //email
  const [Nickname, SetNickname] = useState("");  //nick
  const [Adminpassword, SetAdminpassword] = useState(""); //adminpassword



  const history = useHistory();

  // 
  const emailHandler = (e) => {
    e.preventDefault();
    SetEmail(e.target.value);
  };

  //
  const passwordHandler = (e) => {
    e.preventDefault();
    SetPw(e.target.value);
  };

  //
  const IDHandler = (e) => {
    e.preventDefault();
    SetId(e.target.value);
  };

  //
  const NicknameHandler = (e) => {
    e.preventDefault();
    SetNickname(e.target.value);
  };

  //
  const AdminpasswordHandler =(e)=>{
    e.preventDefault();
    SetAdminpassword(e.target.value);
  }

  //mbti
  const mbtiHandler = (e)=>{
    e.preventDefault();
    SetValue(e.target.value);
  }


  //
  const submitHandler = (e) => {
    e.preventDefault();


    if(Id===""){
      alert(" ");
    }
    else if(Pw===""){
      alert(" ");
    }
    else if(Email===""){
      alert(" ");
    }
    else if(Nickname===""){
      alert(" ");
    }
    else if(value===""){
      alert("MBTI ");
    }
    else if(Adminpassword===""){
      alert("  ");
    } 
    else if(Adminpassword!=="001116"){
      alert("   .")
    }
    else{
          
      Axios({
        method: 'post',
        url: process.env.REACT_APP_DB_HOST + '/api/user/signup',
        data: {
            email: Email,
            password: Pw,
            mbti:value,
            nickname:Nickname,
            username: Id,
            adminPassword: Adminpassword
        },
      })
      .then((Response)=>{
          alert("");
          history.replace("/login");    
      })
      .catch((error)=>{
          alert(" ");
          // console.log(Id);
          // console.log(error);
          
      });

  }
  };



  return (
    
    <>


        {/* sm */}
      <Container component="main" maxWidth="xs"> 
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >


          <img src={process.env.PUBLIC_URL+img1} style={{width:200}}/>


          <Box component="form" onSubmit={submitHandler}>

          <Container maxWidth="xs">
            <div className='inputbox'>
              <input type="text" placeholder="" id="userid" value={Id} onChange={IDHandler} name="userid"></input>
              <label for="userid"></label>
            </div>

            <div className='inputbox'>
              <input type="password" placeholder="" id="password" value={Pw} onChange={passwordHandler}></input>
              <label for="password"></label>
            </div>

            <div className='inputbox'>
              <input type="email" placeholder="" id="email" value={Email} onChange={emailHandler} ></input>
              <label for="email"></label>
            </div>

            <div className='inputbox'>
              <input type="text" placeholder="" id="nickname" value={Nickname} onChange={NicknameHandler} maxLength="10"></input>
              <label for="nickname"></label>
            </div>
            <div className='inputbox'>
              <input type="password" placeholder="" id="adminpassword" value={Adminpassword} onChange={AdminpasswordHandler}></input>
              <label for="nickname"></label>
            </div>
          </Container>
            

    <div>
      <form value={value} onChange={mbtiHandler} style={{margin:"20px 0px 5px 5px"}}>
        
        <select id="mbti" name="mbti" className='selectmbti'>
          <option value="">--  --</option>
          <optgroup label="IN">
            <option value="INTJ">INTJ</option>
            <option value="INTP">INTP</option>
            <option value="INFJ">INFJ</option>
            <option value="INFP">INFP</option>
          </optgroup>

          <optgroup label="IS">
            <option value="ISTJ">ISTJ</option>
            <option value="ISTP">ISTP</option>
            <option value="ISFJ">ISFJ</option>
            <option value="ISFP">ISFP</option>
          </optgroup>

          <optgroup label="EN">
            <option value="ENTJ">ENTJ</option>
            <option value="ENTP">ENTP</option>
            <option value="ENFJ">ENFJ</option>
            <option value="ENFP">ENFP</option>
          </optgroup>

          <optgroup label="ES">
            <option value="ESTJ">ESTJ</option>
            <option value="ESTP">ESTP</option>
            <option value="ESFJ">ESFJ</option>
            <option value="ESFP">ESFP</option>
          </optgroup>    
        </select>
      </form>

      <div>
        <span> MBTI: {value}</span>
      </div>

    </div>
                


    <button class="w-btn w-btn-green" type="submit" style={{marginTop:20, marginBottom:20}}>
        
    </button>

            
            
          </Box>
        </Box>

        


        
      </Container>
            


    </>

  );
};
export default AdminSignup