import React, {useEffect, useState} from 'react';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import "./login.css";
import Axios from "axios";

import img1 from "../images/idfind.jpg"

const PWfind =()=>{

  const [Email, SetEmail] = useState("");

  const emailHandler = (e) => {
    e.preventDefault();
    SetEmail(e.target.value);
  };

  const submitHandler = (e) => {
    if(Email===""){
      alert(" ");
    }
    else{
    
          
      Axios({
        method: 'post',
        url: process.env.REACT_APP_DB_HOST + `/api/user/idFind/${Email}`, // 
        
      })    
      .then((Response)=>{
        SetEmail("");
        alert("  "+Response.data+" ")
      })  
      .catch((error)=>{
        
      })
    }
  }

    return(
        <>

          <Container component="main" maxWidth="xs">
            
            <Box
              sx={{
                marginTop: 8,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
              }}
            >
              

              <img src={process.env.PUBLIC_URL+img1} style={{width:200}}/>


            
              <Box>
              <div className='inputbox'>
                <input type="email" placeholder="" id="email" value={Email} onChange={emailHandler} ></input>
                <label for="email"></label>
              </div>

              <button class="w-btn w-btn-green" type="submit" style={{marginTop:"20px"}} onClick={submitHandler}>
                ID 
              </button>
              </Box>
                
              
            </Box>
          </Container>    
        

        
          
        </>
    );

}


export default PWfind