import React, { useEffect, useState } from "react";
import "./chat.css"
import "./mochat.css"
import { Container, Row, Col} from "react-bootstrap";
import img5 from "../images/chat.png"
import "./modal.css"
import Modal from "./Modal.js"
import ListItemchat from './innerchat/ListItemchat.js';
import AddBoxIcon from '@mui/icons-material/AddBox';
import { useHistory } from "react-router-dom";
import Axios from "axios"
import { useMediaQuery } from "react-responsive"

function useFetch(url) {

    const [data, setData] = useState([]);
    
    async function fetchUrl() {
        const response = await fetch(url);
        const json = await response.json();
        
        setData(json);
    }
    
    useEffect(() => {
        fetchUrl();
    }, []);
    return data;
  }


const Chatmain = () => {

    const [modalOpen, SetmodalOpen] = useState(false);
    const NICKNAME = localStorage.getItem("nickname");
    const TOKEN = localStorage.getItem("token");

    const isMobile = useMediaQuery({
        query : "(max-width:767px)"
      });

    const openModal = () => {
        SetmodalOpen(true);
    };
    
    //  axios mbti
    const closeModal = () => {
        Setroomname("");
        SetmodalOpen(false); 
    };

    const [roomname, Setroomname] = useState("");  //roomname

    const data = useFetch("/chat/rooms"); 

    const history = useHistory();

    // 
    const RoomHandler = (e) => {
        e.preventDefault();
        Setroomname(e.target.value);
    };

    const submitHandler = (e)=>{
        e.preventDefault();

        if(roomname===""){
            alert(" ");
        }
        else{
            Axios({
                method:'post',
                url: process.env.REACT_APP_DB_HOST + `/chat/room?name=${roomname}`,
                headers:{
                    "X-AUTH-TOKEN": TOKEN,
                },
            })
            .then((Response)=>{
                // console.log("  ");
                // console.log(roomname);
                localStorage.setItem("roomId",Response.data.id);
                window.location.reload();
                Setroomname("");
                SetmodalOpen(false); 
            })
            .catch((error)=>{
                alert("  ");
                // console.log(" ");
                // console.log(`/chat?name=${roomname}`);
                // console.log(TOKEN)
                // console.log(error);
                

            })
        }
    }

    const enterinput = e =>{
        if(e.key==='Enter'){
            submitHandler(e);
        }
    }


  return (
    <>
    {   (isMobile)?
        <Container>
            <div className="chatting backin">
                <div className="banner">
                    <img src={process.env.PUBLIC_URL+img5} height="150" />
                </div>

                <Row>
                <Col className="sickbang2">
                    <div className="back2">
                        <h1 className="test11" src={process.env.PUBLIC_URL+img5} height="30" width="30"> <AddBoxIcon onClick={openModal} style={{marginLeft:"70px"}}/><span> </span></h1>

                        <div className="mbticontent">
                            <Modal open={modalOpen} close={closeModal} header=" " submit={submitHandler}>
                                <h5>   !</h5>
                                <input 
                                    type="text" 
                                    placeholder="" 
                                    maxlength="14" 
                                    style={{width:"250px"}} 
                                    value={roomname} 
                                    onChange={RoomHandler}
                                    onKeyPress={enterinput}
                                />
                            </Modal>
                        </div>   

                            <div className="in3">
                                <section className="list-wrapper">
                                    {data.map(
                                        (item, index) => (
                                            <ListItemchat
                                                roomName={item.roomName}
                                                roomId={item.roomId}
                                                // creator={item.creator}
                                                // createDate={item.createDate}
                                                key={item.roomName}
                                            />
                                        )
                                    )}
                                </section>
                            </div>
                    </div>
                </Col>

            </Row>

            </div>
        </Container>
        :
        <Container>
            <div className="chatting backin">
                <div className="banner">
                    <img src={process.env.PUBLIC_URL+img5} height="150" />
                </div>
            <div className="chatcontainer">
                <div className="sickbang">
                    <div className="back">
                        <h1 className="test11" src={process.env.PUBLIC_URL+img5} height="30" width="30"> <AddBoxIcon onClick={openModal} style={{marginLeft:"70px"}}/><span> </span></h1>

                        <div className="mbticontent">
                            <Modal open={modalOpen} close={closeModal} header=" " submit={submitHandler}>
                                <h3>      </h3>
                                <br></br>
                                <h5>   !</h5>
                                <input 
                                    type="text" 
                                    placeholder="" 
                                    maxlength="14" 
                                    style={{width:"250px"}} 
                                    value={roomname} 
                                    onChange={RoomHandler}
                                    onKeyPress={enterinput}
                                />
                            </Modal>
                        </div>   

                            <div className="in2">
                                <section className="list-wrapper">
                                    {data.map(
                                        (item, index) => (
                                            <ListItemchat
                                                roomName={item.roomName}
                                                roomId={item.roomId}
                                                // creator={item.creator}
                                                // createDate={item.createDate}
                                                key={item.roomName}
                                            />
                                        )
                                    )}
                                </section>
                            </div>
                    </div>
                </div>

            </div>
            </div>
        </Container>
    }
    </>
    
  );
};
export default Chatmain;