import React, { useState } from "react";
import {Container, Form} from "react-bootstrap";
import "../write/writes.css"
import { Input,Row, Col  } from "antd"
import Axios from 'axios';
import { useHistory } from "react-router-dom";


const { TextArea } = Input

const WRITEINTJ = () => {

  const MBTI = localStorage.getItem("mbti");
  const NICKNAME = localStorage.getItem("nickname");
  const TOKEN = localStorage.getItem("token");

  const history = useHistory();

  const [post,setPost] = useState({
    title:'',
    desc:'',
    photos:[]
  });


  const [highlight, setHighlight]= useState(false);
  const {title, desc, photos}=post;

  const handlechange = e =>{
    setPost({
      ...post,
      [e.target.name]: e.target.value
    })
  }

  const [files, setFiles] = useState([]);

  const onInputChange = (e) => {
    // console.log(e.target.files)
      let files = e.target.files;
      setFiles(e.target.files)

      handfiles(files);
  };
  

  const handfiles=files=>{
    let photosArr =[];
    for(let file of files){
      let reader = new FileReader();
      reader.readAsDataURL(file);
      reader.addEventListener('load',()=>{
        let fileobj={
          name: file.name,
          type: file.type,
          size: file.size,
          src: reader.result
        }
        photosArr.push(fileobj);
        
        setPost({
          ...post,
          photos: [...photos, ...photosArr]
        })
      });

    }
  }


  

  const submitHandler=async(e)=>{
    e.preventDefault();

    const data = new FormData();

      for(let i = 0; i < files.length; i++) {
          data.append('files', files[i]);
      }
      // console.log(files.length);
    if(title===""){
      alert(" ");
    }
    else if(desc===""){
      alert(" ");
    }  
    // else if(files.length===0){
    //   alert(" 1 ");
    // }
    else{
      Axios({
            method: 'post',
            url: process.env.REACT_APP_DB_HOST + `/api/post/save`,
            headers:{
              "X-AUTH-TOKEN":TOKEN,
            },
            data: {
              title: title,
              content: desc,
              nickname: NICKNAME,
              category: "NORMAL",
            },
          })
          .then((Response)=>{

            // console.log(title);
            // console.log(desc);
            // console.log("")
            // console.log(Response.data);
            alert(" ");
            
            localStorage.setItem("filelength",files.length);
            const filecount = localStorage.getItem("filelength");

            // console.log(filecount);

            history.replace(`/stitle`);

              // console.log(" ");

                Axios({
                  method:'post',
                  url: process.env.REACT_APP_DB_HOST + `/api/file/save/${Response.data}`, 
                  headers: {
                    "Content-Type": "multipart/form-data",
                    "X-AUTH-TOKEN":TOKEN,
                  },
                  data: data, 
                })
                  .then(response => {
                      // console.log(response.data);
                      // console.log(" ");
                      window.location.reload();
                      
                  })
                  .catch(error => {
                      // console.log(error);
                      // console.log(" ");
                    
                  })


          })
          .catch((error)=>{
              alert("");
          });
        }

  }

  
  return (
    <Container>
        <div style={{margin:"auto auto 100px auto"}}>
        <div className="file-upload" style={{marginTop:30}}>
        {/* Image Drag & Drop & Preview */}
        <h2> </h2>
        <span style={{marginBottom:10}}>{MBTI}</span> 
        
            <div className="custom-form-group" >
                <input type="text" name="title" placeholder="Title" maxLength={20} value={title} onChange={handlechange}/>
            </div>
            
            <div className="custom-form-group">
                <div className="form-group files">
                  <label htmlFor="filephotos"><span>Upload</span></label>
                    <input type="file"
                     onChange={onInputChange}
                     className="form-control"
                     multiple/>
                </div>
              
                <div className="custom-file-preview">
                  {photos.length > 0 && photos.map((item,index)=>(
                    <div className="prev-img" key={index} data-imgindex={index}>
                      <img src={process.env.PUBLIC_URL+item.src} alt={item.name}/>
                    </div>
                  ))}
                    
                </div>
            </div>
              
              
            
            <textarea
              type="text"
              placeholder="1000 "
              name="desc"
              value={desc}
              onChange={
                handlechange}
              maxLength={999}
              className='custom-form-group inputh'
                autoSize={{ minRows: 6, maxRows: 6 }}
              />
              <span style={{marginLeft:"auto", marginBottom:20}}>{desc.length}/1,000</span>

            <button type="submit" className="btn-submit" onClick={submitHandler}>Submit</button>
        
        </div>
        </div>
        </Container>

  );
}

export default WRITEINTJ
