import React, {useEffect, useState} from 'react';
import { useHistory } from "react-router-dom";
import CssBaseline from '@mui/material/CssBaseline';
import {Link} from "react-router-dom";
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Axios from 'axios';

import "./login.css";
import img1 from "../images/login.png"


const Login = (props) => {
  const [Id, SetId] = useState("");  //Id
  const [Pw, SetPw] = useState("");  //pw
  
  const history = useHistory();

 

  //
  const passwordHandler = (e) => {
    e.preventDefault();
    SetPw(e.target.value);
  };

  //
  const IDHandler = (e) => {
    e.preventDefault();
    SetId(e.target.value);
  };


  //
  const submitHandler = (e) => {
    e.preventDefault();
    // state   .
    // console.log(Id);
    // console.log(Pw);

    let body = {
          username: Id,
          password: Pw,  
    };

    Axios({
      method: 'post',
      url: process.env.REACT_APP_DB_HOST + '/api/user/login',
      data: {
          username: Id,
          password: Pw,
      },
  })
  .then((Response)=>{

      // console.log(Response.data);
      localStorage.setItem("token",Response.data.token)
      const TOKEN = localStorage.getItem("token");

      localStorage.setItem("mbti",Response.data.mbti)

      const users = Response.data;
      localStorage.setItem("nickname", users.nickname);
      const NICKNAME = localStorage.getItem("nickname");

      // console.log(Response.data.role);

      localStorage.setItem("adminrole",Response.data.role);
      const ADMINROLE = localStorage.getItem("adminrole");

      // console.log(""+ADMINROLE);
      
      window.alert(`${users.nickname} .`);
      history.replace("/");
      window.location.reload();
      // console.log(users.nickname);
  })
  .catch((error)=>{
      alert(" ");
      // console.log(error);
  });
  };



  return (
    
    <>

      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <img src={process.env.PUBLIC_URL+img1} style={{width:200}}/>



          {/*  */}

          <Box component="form" onSubmit={submitHandler}>
            <Container maxWidth="xs">

              <div className='inputbox'>
                <input type="text" placeholder="" id="userid" value={Id} onChange={IDHandler} name="userid"></input>
                <label for="userid"></label>
              </div>

              <div className='inputbox'>
                <input type="password" placeholder="" id="password" value={Pw} onChange={passwordHandler}></input>
                <label for="password"></label>
              </div>

            </Container>
            
            <button class="w-btn w-btn-green" type="submit" style={{marginTop:20, marginBottom:20}}>
                
            </button>
            <Grid container>
              
              
              <Grid item xs>
                
                <Link to="/PWfind">
                  <span style={{marginRight:"5px"}}></span>
                </Link>

              </Grid>
              

              
              <Grid item>
                <Link to="/Signup">
                  <span></span>
                </Link>
              </Grid>
              
            </Grid>
          </Box>
        </Box>
      </Container>
    


  
      

      

    </>

  );
};
export default Login